import axios from "axios"
const Domain = window.location.hostname;
export default class APIservice{
    static async getPlaylistList() {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/list`).then(response=>{return response.data})      
    }
    static async getPlaylist(id) {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/playlist/${id}`).then(response=>{return response.data})  
    }
    static async getTrack(id) {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/track/${id}`).then(response=>{return response.data})  
    }
    static async getArtist(id) {
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/artist/${id}`).then(response=>{return response.data});  
    }
    static async getUserData(name, password){
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/artist/`).then(response=>{return response.data});
    }
    static async search(query, params){
        return axios.get(`https://spotify-django-react-production.up.railway.app/api/search/${query}`, {params:params}).then(response=>{return response.data});
    }
    static async addTrackPlaylist(data, playlistId){
        return axios.post('https://spotify-django-react-production.up.railway.app/api/playlist/add/track', data)
    }
    static async getUserPlaylists(token){
        if(localStorage.getItem('token'))
        {return axios.get('https://spotify-django-react-production.up.railway.app/api/user/playlist/', {headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`,
          }})}
    }
    static async addLikedTrack(data){
        if(localStorage.getItem('token'))
        {return axios.post('https://spotify-django-react-production.up.railway.app/api/user/likedTracks/add/', data, {headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${localStorage.getItem('token')}`,
          }})}
        else {
            alert('please login')
        }
    }
    static async getLikedTracks(){
        if(localStorage.getItem('token'))
        {return axios.get('https://spotify-django-react-production.up.railway.app/api/user/likedTracks/', {headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${localStorage.getItem('token')}`,
          }})}
        else {
            alert('please login')
        }
    }
    static async addTrackToPlaylist(data){
        if(localStorage.getItem('token'))
        {return axios.post('https://spotify-django-react-production.up.railway.app/api/user/playlist/add/track/', data, {headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${localStorage.getItem('token')}`,
          }})}
        else {
            alert('please login')
        }
    }
    static async addDeleteCreatePlaylist(data){
        if(localStorage.getItem('token'))
        {return axios.post('https://spotify-django-react-production.up.railway.app/api/user/playlist/addcreate/', data, {headers: {
            'Content-Type': 'multipart/form-data',
            'Authorization': `Token ${localStorage.getItem('token')}`,
          }})
        }
        else {
            alert('please login')
        }
    }

}