import { useContext , useEffect, useState} from "react";
import CollectionList from "../components/UI/LibraryComs/CollectionList";
import ColList from "../components/UI/LibraryComs/ColList";
import { Authorization } from "../Context/AuthorizationContext";
import '../static/Styles/library.css'
import { PloginContext } from "../Context/SideContexts";
import PCreateModal from "../components/UI/modal/PCreateModal";
import Plogin from "../components/Plogin";
function LibraryPage() {
  const {Authorized, setAuthorized} = useContext(Authorization);
  const {PloginVisible, setPloginVisible} = useContext(PloginContext) 
  const [CreVisible, setCreVisible] = useState(false)

  if(!localStorage.getItem('token')){
    return (<Plogin/>)
  }

  return (
    <div className="library-container">
      <ColList setCreVisible={setCreVisible}/> 
      <CollectionList/>
      <PCreateModal CreVisible={CreVisible} setCreVisible={setCreVisible}/>
    </div>
  );
}

export default LibraryPage;