import React from 'react'
import heart from './icons/heart.svg'
import dots from './icons/dots.svg'
import { useContext } from 'react'
import { TrackListContext } from '../../../Context/baseContext'
import APIservice from '../../../API/APIservice'

const TrackBtnList = ({data, setVisible}) => {
  const {setTracks} = useContext(TrackListContext);
  const playTrack = ()=>{
    setTracks([{TrackId: data.id, cover:data.cover, name:data.name, artist:data.artist}])
  }
  const handeLike = ()=>{
      if(localStorage.getItem('token')){
          APIservice.addLikedTrack({id :data.id, cover:data.cover, name:data.name, artist:data.artist})
      }
  }
  
  return (
    <div className='act_area'>
    <div className='btn_list'>
        <div className='act_btn_playlist'>
                <i className='fa fa-play-circle fa-5x collection_btn' onClick={playTrack}></i>
        </div>
        <div className='act_btn_playlist likedbtn' onClick={handeLike}>
            <img src={heart} alt="" />
        </div>
        <div className='act_btn_playlist editbtn' onClick={()=>{setVisible(true)}}>
            <img src={dots} alt="" />
        </div>
    </div>
    </div>
  )
}

export default TrackBtnList;