import React from 'react'
import { useQuery } from 'react-query'
import APIservice from '../API/APIservice'
import TrackBlock from '../components/TrackBlock'
import PlaylistInfo from '../components/UI/PlaylistComs/PlaylistInfo'
import logImg from '../static/icons/logImg.png'
const LikedSongsPage = () => {
    const playlistData = {cover: logImg, name:'Liked Songs'}
    const {data, isLoading} = useQuery('User-LikedSongs', APIservice.getLikedTracks)
  return (
    <div>
        <PlaylistInfo data={playlistData}/>
        {data?.data.tracks.map((item,index)=>{
                return <TrackBlock data={item} key={index}/>
            })
        }
    </div>
  )
}

export default LikedSongsPage