import PlaylistInfo from '../components/UI/PlaylistComs/PlaylistInfo';
import '../static/Styles/collection.css'
import { useParams, useLocation } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { useQuery } from 'react-query';
import APIservice from '../API/APIservice';
import BtnList from '../components/UI/PlaylistComs/PlaylistBtnList';
import TrackList from '../components/UI/PlaylistComs/TrackList';
import CModal from '../components/UI/modal/Modal';
import PlaylistModal from '../components/UI/PlaylistComs/PlaylistModal';
import PlaylistEditM from '../components/UI/PlaylistComs/PlaylistsM';
function PlaylistPage() {
    const [visible, setVisible] = useState(false)
    const {state} = useLocation()
    const {data, isLoading} = useQuery(`Playlist-${state.uni_id}`, ()=>{let b = APIservice.getPlaylist(state.id); return b;})
    const [editVisible, setEditVisible]  = useState(false)
    if(isLoading){
        return (<h1>loaccib</h1>)
    }

  return (
    <div>
        <PlaylistInfo data={data}/>
        <BtnList data={data} setVisible={setVisible}/>
        <TrackList list={data.tracks} />
        <CModal visible={visible} setVisible={setVisible}>
          {visible? <PlaylistModal data={data} setVisible={setVisible} setEditVisible={setEditVisible}/>: '' }
        </CModal>
        <PlaylistEditM playlist_data={data}  editVisible={editVisible} setEditVisible={setEditVisible}/>
    </div>
  );
}
export default PlaylistPage;